import mtButton from './packages/button/mtButton/index' //
import BackButton from './packages/button/BackButton/index' //
import SaveButton from './packages/button/SaveButton/index' //
import SeleteButton from './packages/button/SelectButton/index' //
import ExportButton from './packages/button/ExportButton/index' //
// import AddButton from './packages/button/AddButton/index' //
import DeletButton from './packages/button/DeleteButton/index' //
import PassButton from './packages/button/PassButton/index' //
import RefuseButton from './packages/button/RefuseButton/index' //
import LoginButton from './packages/button/LoginButton/index' //
import ResetButton from './packages/button/ResetButton/index' //
import LoginoutButton from './packages/button/LogoutButton/index' //

import mtTbale from './packages/table/index' //
import mtPage from './packages/page/index' //

import mtContainer from './packages/container/contain/index' //
import mtListContent from './packages/container/listContent/index' //
import mtSearchForm from './packages/container/searchForm/index' //
import mtEditContent from './packages/container/editContent/index' //
import mtViewContent from './packages/container/viewContent/index' //
import mtToolbar from './packages/container/toolbar/index' //

import mtFormItem from './packages/form/mtFormItem/index' //
import mtForm from './packages/form/mtForm/index' //

import mtCollapse from './packages/collapse/index' //
import mtTopbar from './packages/topbar/index'
import mtNav from './packages/nav/index'
const components = [
  mtButton,
  BackButton,
  SaveButton,
  SeleteButton,
  ExportButton,
  // AddButton,
  DeletButton,
  PassButton,
  RefuseButton,
  LoginButton,
  ResetButton,
  LoginoutButton,
  mtTbale,
  mtPage,
  mtContainer,
  mtListContent,
  mtSearchForm,
  mtEditContent,
  mtViewContent,
  mtToolbar,
  mtFormItem,
  mtForm,
  mtCollapse,
  mtTopbar,
  mtNav
]

const install = function (Vue, opts = {}) {
  components.map(component => {
    Vue.component(component.name, component);
  })
}

/*  */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  mtButton,
  BackButton,
  SaveButton,
  SeleteButton,
  ExportButton,
  // AddButton,
  DeletButton,
  PassButton,
  RefuseButton,
  LoginButton,
  ResetButton,
  LoginoutButton,
  mtTbale,
  mtPage,
  mtContainer,
  mtListContent,
  mtSearchForm,
  mtEditContent,
  mtViewContent,
  mtToolbar,
  mtFormItem,
  mtForm,
  mtCollapse,
  mtTopbar,
  mtNav
}



