
<template>
  <div id="mt-table">
    <el-table
      ref="mt-table"
      :data="data"
      :height="height"
      :max-height="maxHeight"
      stripe
      tooltip-effect="light"
      @selection-change="handleSelectionChange"
    >
      <!--  -->
      <el-table-column type="selection" width="55"></el-table-column>
      <!--  -->
      <slot name="table_oper" />
      <!--  -->
      <template v-for="(item, index) in columns" 02>
        <el-table-column
          v-if="item.show != false"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          :align="item.align?item.align:'center'"
          :width="item.width"
          show
          class="mt-table-th"
        ></el-table-column>
      </template>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'mtTable', //
  props: {
    columns: Array,
    data: Array,
    maxHeight: {
      type: Number,
      default: 2000
    },
    height: {
      type: Number,
      default: 700
    }
  },
  data() {
    return {
      // 
      checkval: null
    }
  },
  methods: {
    handleSelectionChange(val) {
      //
      this.$emit('handleSelectionChange', val)
      this.checkval = val
      console.log(this.checkval)
      // this.$emit('childFn', this.checkval);
      this.$emit('check', this.checkval)
    }
  }
}
</script>


