</template>
<script>
export default {
  name: 'mtContainer' //
}
</script>
<style type="text/css">
.el-container {
  flex-direction: column;
}
html,
body,
#app,
.el-container {
  /*0*/
  padding: 0px;
  /**/
  margin: 0px;
  /*100%*/
  height: 100%;
}
</style>



